import React from 'react'
import './Nav.css'
import {AiOutlineHome} from 'react-icons/ai'
import {AiOutlineUser} from 'react-icons/ai'
import {BiBook} from 'react-icons/bi'
import {MdDesignServices} from 'react-icons/md'
import {BiMessageDetail} from 'react-icons/bi'
import {useState} from 'react'

const Nav = () => {

    const [activeNav, setActiveNav] = useState('#')

    return (
        <nav>
            <a href="#intro" onClick={() => setActiveNav('#intro')} className={activeNav === '#intro' ? 'active' : ''}>{
                <AiOutlineHome/>}</a>
            <a href="#about" onClick={() => setActiveNav('#about')} className={activeNav === '#about' ? 'active' : ''}>{
                <AiOutlineUser/>}</a>
            <a href="#experience" onClick={() => setActiveNav('#experience')}
               className={activeNav === '#experience' ? 'active' : ''}>{<BiBook/>}</a>
            <a href="#projects" onClick={() => setActiveNav('#projects')}
               className={activeNav === '#projects' ? 'active' : ''}>{<MdDesignServices/>}</a>
            <a href="#contact" onClick={() => setActiveNav('#contact')}
               className={activeNav === '#contact' ? 'active' : ''}>{<BiMessageDetail/>}</a>
        </nav>
    )
}

export default Nav